import React, { useState, useEffect } from "react";
import { Container, Row, Col, Card, Button, Alert } from "react-bootstrap";
import { useAuth } from "../contexts/AuthContext";
import { Link, useNavigate } from "react-router-dom";
import { db } from "../firebase";
import {
  collection,
  onSnapshot,
  doc,
  getDoc,
  getDocs,
} from "firebase/firestore";
import Schedule from "./Schedule";
import Overview from "./Overview";
import Navigation from "./Navigation";
import Leaderboard from "./Leaderboard";

export const teamData = React.createContext();

export default function Dashboard() {
  const [error, setError] = useState("");
  const { currentUser, logout } = useAuth();
  const [user, setUser] = useState({});
  const [team, setTeam] = useState({ teamId: "", membersArr: [], rounds: [] });
  const [teamsObj, setTeams] = useState({});
  const [loading, setLoading] = useState(true);
  const history = useNavigate();
  const userRef = doc(db, "users", currentUser.uid);

  // Handle Logout
  async function handleLogout() {
    setError("");
    try {
      await logout();
      history("/login");
    } catch {
      setError("Failed to log out");
    }
  }

  useEffect(() => {
    const getUsersAndTeamData = async () => {
      try {
        // Get the current user data
        const userDoc = await getDoc(userRef);
        if (userDoc.exists()) {
          const userData = userDoc.data();
          setUser(userData);

          // Get the team data based on the user's team ID
          if (userData.teamId) {
            const teamDoc = await getDoc(doc(db, "teams", userData.teamId));
            if (teamDoc.exists()) {
              setTeam({
                teamId: userData.teamId,
                membersArr: teamDoc.data().membersArr,
                rounds: teamDoc.data().rounds,
              });
            }
          }
        }
      } catch (err) {
        setError("Failed to load user or team data");
      } finally {
        setLoading(false); // Set loading to false after data is loaded
      }
    };

    const getTeams = async () => {
      try {
        const teamsSnapshot = await getDocs(collection(db, "teams"));
        const teamsData = {};
        teamsSnapshot.forEach((team) => {
          teamsData[team.id] = team.data();
        });
        setTeams(teamsData);
      } catch (err) {
        setError("Failed to load teams data");
      }
    };

    getUsersAndTeamData();
    getTeams();
  }, [userRef]);

  if (loading) {
    return <h1>Loading...</h1>;
  }

  return (
    <>
      <Navigation user={currentUser} />

      <Row>
        <Col>
          {teamsObj && Object.keys(teamsObj).length > 0 && (
            <Leaderboard data={teamsObj} />
          )}
          <Overview data={team} />
        </Col>
        <Col>
          <Card>
            <Card.Body>
              {error && <Alert variant="danger">{error}</Alert>}
              <strong>Email:</strong> {currentUser.email}
              <Link to="/update-profile" className="btn btn-primary w-100 mt-3">
                Update Profile
              </Link>
              <Link to="/add" className="btn btn-primary w-100 mt-3">
                Database
              </Link>
            </Card.Body>
          </Card>
          {teamsObj && Object.keys(teamsObj).length > 0 && (
            <Schedule teamsObj={teamsObj} />
          )}
        </Col>
      </Row>

      <div className="w-100 text-center mt-2">
        <Button variant="link" onClick={handleLogout}>
          Log Out
        </Button>
      </div>
    </>
  );
}
